.services{
  background-color: var(--gray-color);

  &__title{
    margin: 0;
    padding: 50px 0px 80px 0px;
    font-family: Inter;
    font-size: 40px;
    font-weight: 600;
    text-align: center;
  }
  &__number{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    border: 2px solid var(--red-color);
    background-color: #000;
    color: #fff;
    font-family: Inter;
    font-size: 30px;
    font-weight: 600;
    width: 60px;
    height: 60px;

    &-red{
      color: var(--red-color);
    }
  }
  &__list{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 40px;
  }
  &__item{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__text{
    margin: 0;
    max-width: 200px;
    text-align: center;
    font-size: 16px;
    color: var(--light-grey-color);
  }
}
@include tablet{
  .services__title{
    padding: 30px 0px 50px 0px;
    font-family: Inter;
    font-size: 30px;
  }
  .services__list{
    display: block;
  }
}
