.header{
  background-color: #171717;
  color: #fff;
  padding: 30px 10px;

  &__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo{
    color: #fff;
    padding-bottom: 10px;
    border-bottom: 1px solid #D0016E;
    font-family: Inter;
    font-weight: 600;
    font-size: 30px;
  }
  &__phone{
    font-family: Inter;
    font-size: 25px;
    font-weight: 500;
    color: #fff;
    padding-bottom: 10px;
    border-bottom: 1px solid #D0016E;
  }
}
@include big-desktop{
  .header__container{
    justify-content: center;
  }
  .header__logo{
    margin-right: 40px;
  }
}
@include tablet{
  .header__logo{
    display: none;
  }
}
@include small-tablet{
  .header__container{
    flex-direction: column;
    gap: 20px;
  }
}

