.nav{

  &__list{
    display: flex;
  }
  &__item{
    margin-right: 80px;
  }
  &__link{
    font-family: Inter;
    font-size: 20px;
    font-weight: 400;
    color: #fff;
    @include hover{
      color: var(--red-color);
    }
  }
}
@include big-desktop{
  .nav__item{
    margin-right: 20px;
  }
}
@include small-tablet{
  .nav__list{
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
}
