.about{
  background-color: var(--gray-color);
  &__list{
    padding-top: 50px;
  }
  &__item{
    display: flex;
    gap: 80px;
    padding-bottom: 80px;
  }
  &__text{
    width: 50%;
  }
  &__title{
    margin: 0;
    font-family: Inter;
    font-size: 40px;
    font-weight: 400;
    margin-bottom: 20px;
  }
  &__descr{
    margin: 0;
    font-family: Inter;
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
    color: var(--light-grey-color);
    margin-bottom: 10px;
  }
  &__item-serice{
    font-family: Inter;
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
    color: var(--light-grey-color);
  }

}
.swiper__border{
  position: relative;
  margin-left: 100px;
  border-radius: 20px;
  width: 250px;
  height: 320px;
  border: 2px solid var(--red-color);
  margin-bottom: 60px;
}

@include tablet{
  .about__item{
    flex-direction: column;
    align-items: center;
    gap: 20px;

    &:nth-child(2){
      flex-direction: column-reverse;
    }
  }
  .swiper__border{
    margin: 0;
  }
  .about__title{
    text-align: center;
    font-size: 30px;
  }
  .about__text{
   width: 100%;
  }
  .about__descr{
    font-size: 16px;
    line-height: 28px;
  }
  .about__item-serice{
    font-size: 16px;
  }
}



