.photo{
  background-color: #171717;
  position: relative;

  &__list{
    margin: 0 auto;
    padding: 120px 0px;
    max-width: 980px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 40px;
  }

  &__img{
    width: 300px;
    height: 300px;
  }
  &__icon{
    display: block;
    fill: aqua;
  }
}
@include big-desktop{
  .photo__img{
    width: 250px;
    height: 250px;
  }
  .photo__list{
    padding-top: 40px;
  }
  .photo__sotial{
    position: static;
    padding-top: 80px;
    display: flex;
    justify-content: center;
    gap: 10px;
  }
}

