.feedback{
  background-color: var(--gray-color);

  &__container{
    background-image: url('../img/bg.svg');
    background-size: cover;
    background-position: center bottom -100px;
    background-repeat: no-repeat;
  }

  &__border{
    margin: 0 auto;
    border: none;
  }
  &__swiper{
    position: relative;
    left: 10px;
    border-radius: 20px;
    padding: 10px;
    width: 300px;
    height: 500px;
  }
  &__img{
    width: 300px;
    height: 450px;
  }
  &__title{
    margin: 0;
     padding-top: 20px;
    text-align: center;
  }
  &__text{
    margin: 0;
    font-size: 14px;
    font-weight: 700;
    color: var(--red-color);

  }
}





