.hero{
  position: relative;
  background-color: #171717;
  color: #fff;


  &__title{
    margin: 0;
    padding-top: 150px;
    font-family: Inter;
    font-size: 60px;
    font-weight: 400;
    text-align: center;

    span{
     color: var(--red-color);
    }

  }

  &__subtitle{
    display: block;
    text-align: center;
    color: var(--red-color);
    margin: 0 auto;
    max-width: 700px;
    font-family: Inter;
    font-size: 30px;
    font-weight: 400;
    padding-bottom: 20px;


    span{
      color: #fff;
    }

  }
  &__descr{
    margin: 0 auto;
    max-width: 1100px;
    text-align: center;
    font-family: Inter;
    font-size: 30px;
    line-height: 40px;
    font-weight: 400;
    padding-bottom: 40px;

  }
  &__btn{
    padding-bottom: 100px;
  }
  &__link{
    display: block;
    margin: 0 auto;
    border-radius: 30px;
    border: 1px solid #fff;
    padding: 20px 25px;
    font-family: Inter;
    font-size: 20px;
    font-weight: 400;
    width: 200px;
    height: 60px;
    color: #fff;
    background-color: #171717;

    &:hover{
      transition: all 1s;
      background-color: #D0016E;
    }
  }

}

@include tablet{
  .hero__title{
    padding-top: 70px;
    font-size: 50px;
  }
  .hero__subtitle{
    font-size: 24px;
  }
  .hero__descr{
    font-size: 20px;
    line-height: 30px;
  }
}
@include small-tablet{
  .hero__social{
    position: static;
    display: flex;
    padding-top: 30px;
    justify-content: center;
    gap: 10px;
  }
}
@include mobile{
  .hero__title{
    padding-top: 50px;
    font-size: 40px;
  }
  .hero__subtitle{
    font-size: 20px;
  }
  .hero__descr{
    font-size: 16px;
    line-height: 30px;
  }
}
