.price{
  background-color: #171717;

  &__title{
    margin: 0;
    padding: 80px 0px 20px 0px;
    font-size: 40px;
    font-weight: 600;
    text-align: center;
    color: var(--red-color);
  }
  &__subtitle{
    margin: 0;
    padding-bottom: 20px;
    text-align: center;
    font-size: 20px;
    color: #fff;
  }
  &__descr{
    margin: 0 auto;
    max-width: 600px;
    padding-bottom: 20px;
    line-height: 30px;
    font-size: 20px;
    color: #fff;
  }
  &__text-bottom{
    margin: 0 auto;
    text-align: center;
    max-width: 600px;
    line-height: 30px;
    font-size: 20px;
    color: #fff;
    padding-bottom: 60px;
  }
  &__vidio{
    display: block;
    position: static;
    margin: 0 auto;
    padding-bottom: 80px;
  }
}
@include tablet{
  .price__title{
    font-size: 30px;
  }
  .price__descr{
    font-size: 16px;
    padding-bottom: 10px;
  }
  .price__vidio{
    width: 500px;
    height: 350px;
  }
  .price__text-bottom{
    font-size: 16px;
  }
}
@include mobile{
  .price__vidio{
    width: 300px;
    height: 300px;
  }
}
