.team{
  background-color: var(gray-color);

  &__list{
    margin: 0 auto;
    max-width: 900px;
  }
  &__title{
    margin: 0;
    padding: 40px 0px;
    font-size: 40px;
    text-align: center;
  }
  &__name{
    margin: 0;
    font-size: 20px;
    line-height: 30px;
    span{
      display: block;
      font-size: 24px;
      font-weight: 500;
      padding-bottom: 20px;
    }
  }
  &__item{
  display: flex;
  margin-bottom: 60px;
  }
  &__img{
    border-radius: 10px;
    box-shadow: 19px 20px 8px 0px rgba(34, 60, 80, 0.2);
    margin-right: 40px;
    width: 300px;
    height: 400px;
  }
}
@include tablet{
  .team__title{
    font-size: 30px;
  }
  .team__name{
    font-size: 16px;
    span{
      font-size: 20px;
    }
  }
}
@include small-tablet{
  .team__list{
    max-width: 400px;
  }
  .team__img{
    margin: 0;
    width: 280px;
    height: 340px;
    margin-bottom: 60px;
  }
  .team__item{
    flex-direction: column;
    align-items: center;
  }
}
